/* ----------------------------------------------------Scrollbar CSS------------------------------------------------ */

/* width */
::-webkit-scrollbar {
    width: 15px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(145, 88, 4); 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #807c76; 
  }
  
  ::-webkit-scrollbar-corner {
    background: rgb(145, 88, 4); 
  }



/* -------------------------------------------Popup Boxes------------------------------------------ */



/* The Popup (background) */
.modal {
    display: block; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 20; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  }
  
  /* Popup Content */
  .modal-content {
    background-color: rgb(117, 80, 10);
    margin: auto;
    padding: 20px;
    border: .3em solid rgb(158, 148, 129);
    border-radius:10%;
    width: 15%;
    margin-top:20vh;
    font-size:1.5em;
    color:white;
    font-family: 'ZCOOL KuaiLe', cursive;
    text-align:center;
  }

  /* The Close Button */
  .close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    }
      
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
    }


